import React, { useEffect } from 'react';
import * as THREE from 'three';

const ThreeComponent = () => {
  const camera: THREE.PerspectiveCamera = new THREE.PerspectiveCamera(70, 300 / 300, 0.01, 10);
  const scene: THREE.Scene = new THREE.Scene();
  const geometry: THREE.Geometry | THREE.BufferGeometry = new THREE.BoxGeometry(0.3, 0.3, 0.3);
  const material: THREE.Material | THREE.Material[] = new THREE.MeshNormalMaterial();
  const mesh: THREE.Mesh = new THREE.Mesh(geometry, material);

  camera.position.z = 1;
  scene.add(mesh);

  const init = () => {
    renderer.setSize(300, 300);
    document.querySelector('#threed').appendChild(renderer.domElement);
  };

  const animate = () => {
    requestAnimationFrame(animate);
    mesh.rotation.x += 0.01;
    mesh.rotation.y += 0.03;
    renderer.render(scene, camera);
  };

  useEffect(() => {
    setTimeout(() => {
      const renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({ 
        antialias: true, 
        alpha: true
      });
      
      animate(renderer);
    }, 100)

    return () => {

    };
  }, []);

  init();

  return <div id="threed" />;
};

export default ThreeComponent;
